* {
  font-size: 67.5%;
  box-sizing: border-box;
  margin: 0;
}

.App {
  text-align: center;
  background: linear-gradient(to bottom, rgb(41, 98, 163), rgb(26, 65, 124));
  height: 100vh;
}

.world {
  position: fixed;
  top: 50%;
  transition: all 2s ease;
}

.answer {
  position: fixed;
  bottom: 5%;
  left: 67.5%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.4);
  padding: 1.5rem;
  padding-top: 0rem;
  border-radius: 0.4rem;
}

.country-desc {
  display: flex;
}

.flag {
  height: 7rem;
  margin-left: 2rem;
}

.country-name {
  color: white;
  font-size: 4rem;
  margin: auto;
}

.capital-city-name {
  color: white;
  font-size: 2.2rem;
  margin: auto;
}

.question {
  background: linear-gradient(to top left, #bcd8e9, #d9ecf5);
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 35%;
  padding: 2rem;
}

.title {
  padding-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title-logo {
  height: 9rem;
}

.title-name {
  font-size: 6rem;
  margin-left: 2rem;
  font-family: "Poiret One", cursive;
  font-weight: bold;
}

.question-box {
  width: 100%;
  height: 20rem;
  margin: auto;
  margin-top: 6rem;
  background: linear-gradient(
    to top left,
    rgb(11, 95, 163),
    rgb(125, 204, 228)
  );

  border-radius: 1.2rem;
  box-shadow: 0.2rem 0.2rem 1rem rgba(0, 0, 0, 0.3);
  padding: 1.2rem;
}

.inner-question-border {
  border: 1px solid white;
  border-radius: 0.8rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.written-question {
  color: white;
  font-size: 3rem;
  padding: 2rem;
}

.options-wrapper {
  margin-top: 3rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.option {
  height: 12rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    to top left,
    rgb(183, 210, 235),
    rgb(137, 225, 240)
  );
  margin: 1rem;
  border-radius: 1.6rem;
  cursor: pointer;
  border: 1px solid rgb(134, 186, 230);
}

.option:hover {
  background: linear-gradient(
    to top left,
    rgb(160, 197, 231),
    rgb(108, 216, 235)
  );
}

.no-select {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.written-option {
  font-size: 2rem;
  font-weight: bold;
  color: rgb(1, 61, 95);
}

.reset-btn {
  margin-top: 3rem;
  height: 6rem;
  width: 16rem;
  font-size: 1.5rem;
  font-weight: bold;
  border: 1px solid rgb(149, 177, 219);
  padding: 1rem;
  color: black;
  background: linear-gradient(
    to top left,
    rgb(252, 217, 153),
    rgb(245, 245, 149)
  );
  border-radius: 0.4rem;
  box-shadow: 1rem 1rem 1rem rgba(0, 0, 0, 0.3);
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
}

.reset-btn:hover {
  background: linear-gradient(
    to top left,
    rgb(245, 204, 129),
    rgb(247, 247, 135)
  );
}

.reset-btn:active {
  transform: translateY(2%);
  box-shadow: 0.5rem 0.5rem 0.5rem rgba(0, 0, 0, 0.3);
}
